import eventsEngine from "../core/events_engine";
import { removeEvent } from "../remove";

function nodesByEvent(event) {
  return event && [event.target, event.delegateTarget, event.relatedTarget, event.currentTarget].filter(function (node) {
    return !!node;
  });
}

export var subscribeNodesDisposing = function subscribeNodesDisposing(event, callback) {
  eventsEngine.one(nodesByEvent(event), removeEvent, callback);
};
export var unsubscribeNodesDisposing = function unsubscribeNodesDisposing(event, callback) {
  eventsEngine.off(nodesByEvent(event), removeEvent, callback);
};